@keyframes scanline {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100%);
  }
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}

@keyframes glitch {
  0% {
    text-shadow: 0.05em 0 0 #88ff00, -0.03em -0.04em 0 #00d9ff,
                 0.025em 0.04em 0 #ff0000;
  }
  15% {
    text-shadow: 0.05em 0 0 #59ff00, -0.03em -0.04em 0 #ff0000,
                 0.025em 0.04em 0 #fffc00;
  }
  16% {
    text-shadow: -0.05em -0.025em 0 #37ff00, 0.025em 0.035em 0 #ff0000,
                 -0.05em -0.05em 0 #fffc00;
  }
  49% {
    text-shadow: -0.05em -0.025em 0 #77ff00, 0.025em 0.035em 0 #ff0000,
                 -0.05em -0.05em 0 #fffc00;
  }
  50% {
    text-shadow: 0.05em 0.035em 0 #6aff00, 0.03em 0 0 #00ffeeb4,
                 0 -0.04em 0 #fffc00;
  }
  99% {
    text-shadow: 0.05em 0.035em 0 #99ff00, 0.03em 0 0 #00ff1a8b,
                 0 -0.04em 0 #fffc00;
  }
  100% {
    text-shadow: -0.05em 0 0 #a2ff00, -0.025em -0.04em 0 #03a81e,
                 -0.04em -0.025em 0 #fffc00;
  }
}

/* Custom Cursor Styles */
body {
  cursor: none; /* Hide the default cursor */
}

.custom-cursor {
  width: 32px;
  height: 32px;
  position: fixed;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
  transition: all 0.1s ease;
  background-image: url('./assets/kopf.png');
  background-size: contain;
  background-repeat: no-repeat;
}

.custom-cursor.scanning {
  background-image: url('./assets/scent.png');
  transform: scale(1.2);
}

.floating-dialogue-container {
  position: fixed;
  bottom: 1.5rem;
  right: 1.5rem;
  background: rgba(120, 117, 111, 0.291);
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 20px rgba(212, 211, 199, 0.95),
              inset 0 0 15px rgba(251, 255, 0, 0.1);
  width: 250px;
  z-index: 1000;
  font-family: 'DotGothic16', monospace;
  color: #48ff00;
  border: 1px solid rgb(249, 248, 199);
  overflow: hidden;
  cursor: none;
}

.scan-line {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(
    to right,
    transparent,
    rgb(248, 253, 255),
    transparent
  );
  animation: scanline 4s linear infinite;
}

.dialogue-header {
  display: flex;
  justify-content: space-between;
  font-size: 0.6rem;
  margin-bottom: 0.8rem;
  text-transform: uppercase;
  color: rgb(60, 255, 0);
}

.status-text {
  position: relative;
  padding-left: 1rem;
}

.status-text::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  background: #66ff00;
  border-radius: 50%;
  animation: blink 2s infinite;
}

.dialogue-content {
  position: relative;
  min-height: 4rem;
}

.text-display {
  font-size: 0.9rem;
  line-height: 1.3;
  margin-bottom: 0.8rem;
  animation: glitch 3s infinite;
}

.cursor {
  display: inline-block;
  margin-left: 2px;
  font-weight: bold;
}

.cursor.blink {
  animation: blink 1s infinite;
}

.scanning-indicator {
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 0.7rem;
  color: rgb(85, 255, 0);
  animation: blink 1s infinite;
}

.dialogue-footer {
  margin-top: 1rem;
}

.progress-bar {
  height: 2px;
  background: rgba(0, 255, 0, 0.473);
  border-radius: 1px;
  overflow: hidden;
}

.progress {
  height: 100%;
  background: #00ff1e;
  transition: width 0.1s linear;
}

/* Tablet und Mobile Responsiveness */
@media (max-width: 1024px) {
  .floating-dialogue-container {
    width: 200px;
    bottom: 1rem;
    right: 1rem;
    padding: 0.8rem;
  }

  .dialogue-header {
    font-size: 0.5rem;
    margin-bottom: 0.6rem;
  }

  .text-display {
    font-size: 0.8rem;
    line-height: 1.2;
    margin-bottom: 0.6rem;
  }
}

@media (max-width: 768px) {
  .floating-dialogue-container {
    width: 180px;
    bottom: 0.8rem;
    right: 0.8rem;
    padding: 0.6rem;
  }

  .dialogue-header {
    font-size: 0.45rem;
    margin-bottom: 0.5rem;
  }

  .text-display {
    font-size: 0.7rem;
    line-height: 1.1;
    margin-bottom: 0.5rem;
  }
}

@media (max-width: 480px) {
  .floating-dialogue-container {
    width: 160px;
    bottom: 0.6rem;
    right: 0.6rem;
    padding: 0.5rem;
  }

  .dialogue-header {
    font-size: 0.4rem;
    margin-bottom: 0.4rem;
  }

  .text-display {
    font-size: 0.6rem;
    line-height: 1;
    margin-bottom: 0.4rem;
  }
}

/* Add subtle CRT screen effect */
.floating-dialogue-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    0deg,
    rgba(227, 227, 202, 0.223),
    rgba(111, 255, 0, 0.032) 1px,
    transparent 1px,
    transparent 2px
  );
  pointer-events: none;
}

/* Make sure clickable elements show interaction */
a, button, [role="button"], input, select, textarea {
  cursor: none;
  &:hover {
    .custom-cursor {
      transform: scale(1.5);
    }
  }
} 